.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.trigger:hover {
  color: #1890ff;
}
.wrap-content {
  height: calc(100vh - 64px);
  overflow: auto;
}
.content {
  background: #fff;
  padding: 20px;
  margin: 16px;
}
.user {
  padding: 0 24px;
  float: right;
}
